@import url(global.css);

.banner__container {
  height: calc(22vw * 1.72);
}

.banner__container img {
  object-fit: contain;
}

.line {
  height: 2px;
  width: 7.03125vw;
  /* margin: 0 auto; */
  background-color: #4d4d4d;
}

.home-title {
  text-transform: uppercase;
  color: #4d4d4d;
  font-weight: 500;
}

@media only screen and (max-width: 1200px) {
  h2.home-title {
    font-size: 20px;
  }
}

.bran__logo {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 8.4375vw;
  width: 100%;
}

.bran__logo img {
  height: 100%;
  width: 100%;
  object-fit: contain;
}

.bran__logo:hover {
  box-shadow: 0 10px 30px rgb(0 0 0 / 10%);
}

.bran__viewmore {
  font-size: calc(6px + (24 - 10) * ((100vw - 375px) / (1920 - 375)));
  padding: 10px 20px;
  border: 0.5px solid #383838;
}

.bran__viewmore:hover {
  color: var(--blue-dior);
  border-color: var(--blue-dior);
}

@media only screen and (max-width: 992px) {
  .content__wrapper .searchform {
    display: none;
  }

  .line {
    width: 71px;
    height: 1px;
  }
}

@media only screen and (max-width: 768px) {
  .searchform {
    width: 100% !important;
  }

  .bran__viewmore {
    font-size: smaller;
  }

  .bran-list__title {
    font-size: smaller;
  }

  .bran__logo-list > * {
    display: none;
    visibility: hidden;
    height: 0;
    margin: 0;
  }

  .bran__list-img img {
    opacity: 1 !important;
  }

  .bran__list-img {
    height: 13.4375vw !important;
  }

  .bran__list-name {
    font-size: small;
  }

  .bran__list-type {
    font-size: x-small;
  }
}

@media only screen and (max-width: 600px) {
  .bran__list-item {
    width: calc(50% - 10px) !important;
    margin: 0 10px 10px 0px !important;
  }

  .bran__list-img {
    height: 13.4375vw !important;
  }
}

.content__wrapper {
  box-shadow: 0 10px 30px rgb(0 0 0 / 10%);
}

.products__content-wrapper {
  box-shadow: unset !important;
}

.bran__filter h3 {
  font-size: 1.25vw;
  line-height: 1.30208vw;
  margin-right: 16px;
}

.bran__filter .order__item-wrapper {
  width: 225px;
}

.bran__filter svg {
  margin-left: 10px;
}

.bran__filter .order__item-dropdown {
  padding: 0.375rem 0.75rem;
  border: 1px solid rgb(221, 221, 221);
  border-radius: 0.25rem;
  font-size: 1rem;
  line-height: 1.5;
  color: #495057;
}

.bran__filter .order__item-dropdown_menu .order__item-dropdown_items button {
  font-size: 1rem;
  line-height: 1.5;
  color: #495057;
}

.bran__filter
  .order__item-dropdown_menu
  .order__item-dropdown_items
  button:hover {
  color: var(--blue-dior);
}

.content__wrapper .searchform {
  width: 40%;
}

.bran__list-line {
  height: 1px;
  /* margin: 0 auto; */
  background-color: #ddd;
}

.bran__list-item {
  position: relative;
  width: calc(20% - 1.04167vw);
  margin: 0 0.52083vw 1.04167vw;
  background-color: #fafafa;
  border: unset;
}

.bran__list-item:hover a {
  color: #333;
}

.bran__list-img {
  height: 6.4375vw;
}

.bran__list-img img {
  opacity: 0.5;
}

.bran__list-item:hover img {
  opacity: 1;
}

.content__inner {
  padding: 1.5rem 0;
}

.products__content {
  width: 100%;
}

.products__menu {
  width: 17.70833vw;
  /* width: 20%; */
  margin-right: 3.125vw;
}

.web__directional {
  margin-bottom: 1.5rem;
}

.web__directional li {
  display: inline;
  font-size: 0.83333vw;
  line-height: 0.98958vw;
}

.web__directional li a {
  color: #a0a0a0;
  text-decoration: none;
}

.web__directional li:hover a {
  color: var(--blue-dior);
}

.web__directional li + li::before {
  content: "/";
  padding: 0 5px;
  color: #a0a0a0;
}

.product__filter {
  color: #383838;
}

.filter__block:not(:last-child) {
  /* margin-bottom: calc(15px + (20 - 15) * ((100vw - 375px)/ (1920 - 375))); */
}

.filter__block h3 {
  font-size: 1.25vw;
  line-height: 1.30208vw;
  padding-bottom: 10px;
  border-bottom: 1px solid #ddd;
  font-family: "Playfair Display";
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-transform: uppercase;
}

.filter__block:hover h3 {
  color: var(--blue-dior);
}

.filter__block svg {
  height: 1.25vw;
  width: 1.25vw;
  transition: all 0.3s ease;
}

.svg-right {
  transform: rotate(-90deg);
  transition: all 0.3s ease;
}

.filter__block:hover svg {
  stroke: var(--blue-dior);
}
/* .filter__inner{
                        padding-top: calc(10px + (24 - 20) * ((100vw - 375px)/ (1920 - 375)));
                        padding-bottom: calc(10px + (40 - 20) * ((100vw - 375px)/ (1920 - 375))); 
                    } */
.filter__list {
  max-height: 0;
  margin: calc(10px + (24 - 20) * ((100vw - 375px) / (1920 - 375))) 0;
  transition: max-height 0.6s ease;
}

.filter__list,
.filter__sub-list {
  overflow: hidden;
  overflow-y: auto;
}

.filter__item {
  position: relative;
  overflow: hidden;
  padding-bottom: 2px;
}

.filter__item p {
  font-size: 1.2vw;
  font-weight: 500;
}

.sub-item-cate {
  font-size: 1vw;
}

.filter__item:not(:last-child) {
  margin-bottom: calc(5px + (20 - 15) * ((100vw - 375px) / (1920 - 375)));
}

.filter__item::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 1px;
  width: 28px;
  background: #383838;
  transform: translateX(-100%);
  transition: all 0.3s ease;
}

.checkbox input {
  margin-left: calc(12px + (15 - 10) * ((100vw - 375px) / (1920 - 375)));
  margin-right: calc(4px + (15 - 10) * ((100vw - 375px) / (1920 - 375)));
}

.checkbox label > div {
  margin-right: 5px;
  line-height: 22px;
}

.checkbox label {
  cursor: pointer;
  font-size: calc(14px + (20 - 14) * ((100vw - 375px) / (1920 - 375)));
  /* line-height: 1; */
}

.checkbox span {
  font-size: 1vw;
  /* line-height: 1; */
  margin-right: 5px;
  color: #d4d4d4;
}

.filter__sub-item:hover {
  color: var(--blue-dior);
}

.products__list {
  width: calc(100% - 17.70833vw);
}

.filter__control-top {
  position: relative;
}

.filter__control-top,
.filter__control-bottom {
  padding-top: calc(15px + (28 - 15) * ((100vw - 375px) / (1920 - 375)));
  padding-bottom: calc(15px + (28 - 15) * ((100vw - 375px) / (1920 - 375)));
  border-bottom: solid 1px #383838;
  border-top: solid 1px #383838;
  display: flex;
  align-items: center;
}

.filter__control-top .order__item-dropdown {
  min-width: 249px;
}

.filter__number {
  color: #383838;
  font-size: calc(14px + (20 - 14) * ((100vw - 375px) / (1920 - 375)));
  line-height: 1.2;
  display: flex;
  align-items: center;
  margin-right: calc(10px + (40 - 10) * ((100vw - 375px) / (1920 - 375)));
}

.filter__number .text {
  color: inherit;
  margin-right: 6px;
}

.filter__number span {
  display: inline-block;
  color: inherit;
}

.filter__number .number span:first-child {
  position: relative;
}

.filter__number .number span:first-child::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 1px;
  width: 100%;
  background: #383838;
  transition: all 0.3s ease;
}

.filter__number .number span:last-child {
  color: #a0a0a0;
}

.filter__pagination {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 1;
}

.pagination {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-left: 0;
  list-style: none;
}

.pagination-item {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: calc(35px + (45 - 35) * ((100vw - 375px) / (1920 - 375)));
  height: calc(35px + (45 - 35) * ((100vw - 375px) / (1920 - 375)));
  background: #d4d4d4;
  color: #fff;
  margin: 0 calc(2.5px + (13 - 2.5) * ((100vw - 375px) / (1920 - 375)));
}

.pagination-item.next,
.pagination-item.previous {
  background: #fff;
  border: solid 1px #383838;
  color: #383838;
}

.pagination-item.active {
  cursor: default;
}

.pagination-item.active {
  background: #383838;
}

.filter__item-wrapper {
  display: none;
  width: calc(50% - 11px);
}

.filter__dropdown {
  position: relative;
  width: 100%;
  padding: 12px calc(12px + (18 - 12) * ((100vw - 375px) / (1920 - 375))) 8px;
  color: #383838;
  background-color: #fff;
  border: solid 1px #383838;
  font-weight: 400;
  font-size: calc(14px + (20 - 14) * ((100vw - 375px) / (1920 - 375)));
  line-height: calc(17px + (25 - 17) * ((100vw - 375px) / (1920 - 375)));
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
  transition: all 0.3s ease;
  cursor: pointer;
}

.filter__dropdown svg {
  display: inline-block;
  margin-left: calc(0px + (60 - 0) * ((100vw - 375px) / (1920 - 375)));
  width: calc(16px + (18 - 12) * ((100vw - 375px) / (1920 - 375)));
  height: calc(16px + (18 - 12) * ((100vw - 375px) / (1920 - 375)));
  transition: all 0.3s ease;
}

@media only screen and (max-width: 1200px) {
  .content__inner {
    padding: 1.25rem 0;
  }

  .web__directional {
    margin-bottom: 1.25rem;
  }

  .web__directional li {
    font-size: 14px;
    line-height: 18px;
  }

  .filter__control-top {
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 0;
  }

  .filter__number,
  .filter__pagination {
    margin-top: calc(10px + (10 - 20) * ((100vw - 375px) / (1920 - 375)));
  }
}

@media only screen and (max-width: 992px) {
  .bran__filter h3 {
    font-size: 14px;
    line-height: 20px;
  }

  .bran__filter .order__item-dropdown,
  .bran__filter .order__item-dropdown_menu .order__item-dropdown_items button {
    font-size: 12px;
  }

  .products__menu {
    width: 200px;
    margin-right: 30px;
  }

  .web__directional li {
    font-size: 12px;
    line-height: 14px;
  }

  .web__directional li + li::before {
    /* padding: 0 5px 0 0; */
  }

  .filter__block svg {
    height: 14px;
    width: 14px;
  }

  .filter__block h3 {
    font-size: 14px;
    line-height: 20px;
  }

  .filter__item p,
  .sub-item-cate,
  .checkbox span {
    font-size: 12px;
  }
}

@media only screen and (max-width: 768px) {
  .filter__number {
    margin-right: 0;
  }

  .bran__filter {
    flex: wrap;
    width: 100%;
    justify-content: space-between;
    /* margin-bottom: 9px; */
  }

  .bran__filter h3 {
    font-size: 16px;
    line-height: 25px;
    width: 100%;
    margin-right: 0;
  }

  .bran__filter .order__item-wrapper {
    width: 100%;
    margin-bottom: 0.5rem;
  }

  .filter__control-top .order__item-dropdown {
    min-width: unset;
  }

  .bran__filter .order__item-dropdown {
    font-size: 12px;
  }

  .bran__filter .order__item-dropdown_menu .order__item-dropdown_items button {
    font-size: 12px;
  }

  .content__inner {
    padding: 9px 0;
  }

  .web__directional {
    margin-bottom: 9px;
  }

  .products__content {
    flex-direction: column;
  }

  .products__menu {
    width: 100%;
    margin-right: 0;
  }

  .filter__block:not(:last-child) {
    margin-bottom: 0;
  }

  .products__list {
    width: 100%;
  }

  .filter__control-top,
  .filter__control-bottom {
    border-top: solid 1px #ddd;
    border-bottom: solid 1px #ddd;
    padding: 9px 0;
  }

  .filter__control-bottom {
    border-top: none;
    justify-content: space-between;
  }

  .filter__item-wrapper {
    margin-right: 0;
    width: calc(50% - 11px);
  }

  .filter__dropdown {
    border: solid 1px #d4d4d4;
  }

  .filter__pagination {
    width: calc(50% - 11px);
    justify-content: unset;
    flex: unset;
  }

  .pagination {
    width: 100%;
    justify-content: space-between;
  }

  .pagination-item {
    width: calc(30px + (45 - 35) * ((100vw - 375px) / (1920 - 375)));
    height: calc(30px + (45 - 35) * ((100vw - 375px) / (1920 - 375)));
    font-size: 14px;
  }
}

@media only screen and (max-width: 480px) {
  .pagination-item {
    width: 26px;
    height: 26px;
    font-size: 12px;
  }
}

/* Signup */
.signup__wrapper {
  margin-bottom: 300px;
  /* box-shadow: 0 10px 30px rgb(0 0 0 / 10%); */
}

.signup__wrapper .web__directional {
  padding-bottom: 9px;
  border-bottom: 1px solid #ddd;
}

.signup__inner {
  max-width: 442px;
  background: #f8f8f8;
  color: #383838;
}

.signup__inner input,
.signup__inner button {
  font-size: calc(4px + (24 - 10) * ((100vw - 375px) / (1920 - 375)));
}

.signup__inner input,
.signup__inner select {
  border: 1px solid #ddd;
  padding: 5px 10px;
}

.signup__inner input:focus-within {
  outline: none;
  border: 1px solid var(--blue-dior);
  box-shadow: 0 10px 30px rgb(0 0 0 / 10%);
}

.signup__inner h3 {
  font-size: calc(10px + (24 - 10) * ((100vw - 375px) / (1920 - 375)));
  color: #383838;
  margin-bottom: 1.5rem;
}

.signup__inner button:focus {
  outline: none;
}

.signup__inner button {
  background-color: #4d4d4d;
  color: #fff;
  /* border: 1px solid #333; */
  padding: 5px 10px;
  cursor: pointer;
}

.signup__inner button:hover {
  box-shadow: 0 10px 30px rgb(0 0 0 / 10%);
}

.verifycode-signup {
  visibility: hidden;
}

.verifycode-signup input {
  width: 65%;
}

.form-basic {
  position: relative;
  margin-bottom: 1rem;
}

.form-basic input,
.form-basic select {
  padding:  0.5rem 1rem;
}

.form-input {
  position: relative;
  border-bottom: 1px solid #ddd;
}

.form-input input {
  border: unset !important;
  padding: 0.5rem 2rem;
}

.form-input input:focus {
  box-shadow: unset !important;
}

.form-input span {
  position: absolute;
  left: 0;
  height: 100%;
  align-items: center;
  display: flex;
}

.signup-info {
  font-size: calc(16px + (20 - 16) * ((100vw - 375px) / (1920 - 375)));
  line-height: 25px;
  color: #383838;
  margin-bottom: calc(10px + (17 - 10) * ((100vw - 375px) / (1920 - 375)));
}

.signup-block:not(:last-child) {
  margin-bottom: calc(22px + (17 - 10) * ((100vw - 375px) / (1920 - 375)));
}

@media only screen and (max-width: 768px) {
  .signup__inner h3 {
    font-size: 16px;
    margin-bottom: 0.5rem;
  }

  .signup__inner input,
  .signup__inner button {
    font-size: 14px;
  }
}

.product__grid-wrapper {
  padding: calc(16px + (78 - 16) * ((100vw - 375px) / (1920 - 375))) 0
    calc(30px + (25 - 30) * ((100vw - 375px) / (1920 - 375)));
}

.product__grid-title {
  text-transform: uppercase;
  font-size: calc(14px + 14 * ((100vw - 375px) / 1545));
  margin-bottom: calc(16px + (78 - 16) * ((100vw - 375px) / (1920 - 375)));
}

.product__grid-title:not(:first-child) {
  margin-top: calc(16px + (78 - 16) * ((100vw - 375px) / (1920 - 375)));
}

.product__grid-title::after,
.product__grid-title::before {
  background-color: #ddd;
  content: "";
  display: inline-block;
  height: 1px;
  position: relative;
  vertical-align: middle;
  width: 10%;
}

.product__grid-title::before {
  right: 0.5em;
  margin-left: -50%;
}

.product__grid-title::after {
  left: 0.5em;
  margin-right: -50%;
}

.product {
  position: relative;
  margin-right: 4px;
  margin-bottom: 6px;
  background-color: #fff;
}

.product-3 {
  width: calc(100% / 3 - 4px);
}

.product-4 {
  width: calc(100% / 4 - 4px);
}

.product-heart {
  margin-right: 10px;
  height: 24px;
  width: 24px;
}

.product-label {
  position: absolute;
  top: 0;
  right: 0;
  align-items: center;
}
/* .product-label span{
            background-color: #ececec;
            padding: 5px 10px;
            margin-right: 10px;
            border:1px solid #ddd;
        } */
.product-label span {
  background-color: #ac0833;
  padding: 2px 6px;
  margin-right: 10px;
  font-size: 14px;
  color: #fff;
}

.product-disc {
  position: absolute;
  top: 0;
  left: 2.70833vw;
  width: 2.08333vw;
  height: 2.08333vw;
  font-size: calc(16px + (22 - 16) * ((100vw - 375px) / (1920 - 375)));
  line-height: calc(22px + (32 - 22) * ((100vw - 375px) / (1920 - 375)));
  display: flex;
  justify-content: center;
  align-items: flex-end;
  background: red;
  height: calc(30px + (45 - 30) * ((100vw - 375px) / (1920 - 375)));
  width: calc(45px + (65 - 45) * ((100vw - 375px) / (1920 - 375)));
  color: #fff;
}

.product-disc:before {
  border-top: calc(15px + (20 - 15) * ((100vw - 375px) / (1920 - 375))) solid
    red;
  border-left: calc(22.5px + (32.5 - 22.5) * ((100vw - 375px) / (1920 - 375)))
    solid transparent;
  border-right: calc(22.5px + (32.5 - 22.5) * ((100vw - 375px) / (1920 - 375)))
    solid transparent;
  content: "";
  height: 0;
  left: 0;
  position: absolute;
  bottom: calc(-15px + (-20 - -15) * ((100vw - 375px) / (1920 - 375)));
  width: 0;
}

.product:hover {
  box-shadow: 0 10px 30px rgb(0 0 0 / 10%);
}

.product-img {
  box-sizing: border-box;
  height: calc(233px + (468 - 233) * ((100vw - 375px) / (1920 - 375)));
  position: relative;
  display: block;
  width: 100%;
  overflow: hidden;
  text-align: center;
}

.fill {
  fill: #333;
}

.product-img img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: inline-block;
  margin: auto;
  max-width: 100%;
  max-height: 100%;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: contain;
  -o-object-fit: contain;
  object-fit: contain;
}

.product__info-benefit a {
  font-size: 0.83333vw;
  line-height: 1.04167vw;
  color: var(--blue-dior);
}

.product__info-detail {
  padding: 10px calc(7px + (20 - 7) * ((100vw - 375px) / (1920 - 375))) 15px;
}

.product-brand {
  color: #383838;
  font-size: 20px;
  line-height: 24px;
  margin-bottom: 0.52083vw;
}

.product-name {
  display: block;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 18px;
  line-height: 22px;
  color: #707070;
  margin-bottom: 1.04167vw;
}

.product-subprice {
  font-size: 16px;
  line-height: 19px;
  text-decoration: line-through;
  color: #a0a0a0;
}

.product-price {
  font-size: 20px;
  line-height: 24px;
  color: #4d4d4d;
  /* margin-bottom: 0.625vw; */
}

.product-btn {
  height: calc(43px + (54 - 43) * ((100vw - 375px) / (1920 - 375)));
  margin-top: 0.78125vw;
}

.product-btn button {
  transform: scale(1);
  opacity: 1;
  transition: all 0.5s ease;
  background: #fff;
  color: var(--blue-dior);
  border: 1px solid #383838;
  display: inline-block;
  font-size: 0.9375vw;
  line-height: 1;
  padding: 0.75521vw 2.8125vw;
  border: 1px solid #383838;
}

.product-btn:hover a {
  box-shadow: 0 10px 30px rgb(0 0 0 / 10%);
}

@media only screen and (max-width: 1200px) {
  .product-subprice {
    font-size: 12px;
    line-height: 14px;
  }

  .product-price {
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 8px;
